@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 256px 256px 256px;
    grid-template-rows: 256px 256px 256px 256px;
  }

  .advantages__item:nth-child(1) {
    grid-column: 1/4;
    display: flex;
  }

  .advantages__item:nth-child(2) {
    grid-column: 1/2;
    grid-row: 2/3;
  }

  .advantages__item:nth-child(3) {
    grid-column: 2/3;
    grid-row: 2/3;
  }

  .advantages__item:nth-child(4) {
    grid-column: 3/4;
    grid-row: 2/3;
  }

  .advantages__item:nth-child(5) {
    grid-column: 1/4;
    grid-row: 3/4;
    display: flex;
  }

  .advantages__item:nth-child(2),
.advantages__item:nth-child(3),
.advantages__item:nth-child(4) {
    text-align: center;
  }

  .advantages__item__header {
    width: 256px;
    height: 256px;
    box-sizing: border-box;
  }

  .advantages__image-city,
.advantages__image-bridge {
    height: 256px;
    flex-grow: 1;
  }

  .advantages-bridge {
    order: 2;
  }

  .advantahes__item__posts {
    width: 256px;
    height: 256px;
    box-sizing: border-box;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 400px 400px 400px;
    grid-template-rows: 256px 256px 256px 256px;
