.social__link--vkontakte {
  position: relative;
  &::before {
    position: absolute;
    content: "";
    width: 48px;
    height: 48px;
    left: -12px;
    top: -12px;
    background-color:  #81B3D2;
  }
  &::after {
    position: absolute;
    background-image: url("../img/footer-images/vkontakte.svg");
    content: "";
    width: 24px;
    height: 24px;
  }
}

